#playfield{
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(4, 1fr);
  width: 450px;
  height: 450px;
  background: gray;
  border-radius: 10px;
  padding: 5px;
}

.thing{
  width: 100px;
  height: 100px;
  background: transparent;
  color: gray;
  text-align: center;
  font: 48px/96px Arial;
  border-radius: 10px;
  align-items: center;
  margin: 5px;
}
.thing.t2{
  background: #ffffff;
}
.thing.t4{
  background: #e6ffff;
}
.thing.t8{
  background: #ccffff;
}
